import {
  CameraShake,
  Center,
  Environment,
  OrbitControls,
  PerspectiveCamera,
  PivotControls,
  Preload,
  useGLTF,
  View,
} from '@react-three/drei';
import { Canvas, GroupProps, PrimitiveProps, useFrame } from '@react-three/fiber';
import * as React from 'react';
import { Group, Mesh } from 'three';
import Area from './Area';
import { keyframes } from '@emotion/react';

export type Props = {};

declare module 'three-stdlib/loaders/GLTFLoader' {
  interface GLTF {
    nodes: {
      [key: string]: Mesh;
      // Mesh_sodaBottle: {
      //   geometry: ;
      // };
      // Mesh_sodaBottle_1: {
      //   geometry: any;
      // };
    };
  }
}

function useHover() {
  const [hovered, hover] = React.useState(false);
  return [hovered, { onPointerOver: () => hover(true), onPointerOut: () => hover(false) }] as const;
}

function Soda(props: GroupProps) {
  const ref = React.useRef<Group>(null);
  const [hovered, spread] = useHover();
  const { nodes, materials } = useGLTF(
    'https://vazxmixjsiawhamofees.supabase.co/storage/v1/object/public/models/soda-bottle/model.gltf'
  );
  useFrame((state, delta) => {
    const group = ref.current;
    if (!group) {
      return;
    }
    group.rotation.y = (group.rotation.y || 0) + delta;
  });
  const node = nodes.Mesh_sodaBottle as Mesh;
  return (
    <group ref={ref} {...props} {...spread} dispose={null}>
      <mesh geometry={node.geometry}>
        <meshStandardMaterial color={hovered ? 'red' : 'green'} roughness={0.33} metalness={0.8} envMapIntensity={2} />
      </mesh>
      <mesh geometry={node.geometry} material={materials.red} material-envMapIntensity={0} />
    </group>
  );
}

export function Duck(props: Omit<PrimitiveProps, 'object'>) {
  const { scene } = useGLTF('https://vazxmixjsiawhamofees.supabase.co/storage/v1/object/public/models/duck/model.gltf');
  return <primitive object={scene} {...props} />;
}

export function Candy(props: Omit<PrimitiveProps, 'object'>) {
  const { scene } = useGLTF(
    'https://vazxmixjsiawhamofees.supabase.co/storage/v1/object/public/models/candy-bucket/model.gltf'
  );
  useFrame((state, delta) => (scene.rotation.z = scene.rotation.y += delta));
  return <primitive object={scene} {...props} />;
}

